import React, { Fragment } from 'react';
import { useHistory } from 'react-router-dom';
import { AppBar, Icon, IconButton, Toolbar } from '@material-ui/core';
import { useStyles } from './MainHeaderStyles';

function MainHeaderComponent(props) {
    const classes = useStyles();
    const history = useHistory();
    return (
        <Fragment>
            <AppBar color='default' position='sticky'>
                <Toolbar>
                    <div className={classes.main}>MainHeaderComponent</div>
                    {props.pageList.map(item => (
                        <IconButton
                            key={item.key}
                            onClick={() => {
                                history.push(item.path);
                            }}
                            children={<Icon children={item.icon}/>}
                        />
                    ))}
                </Toolbar>
            </AppBar>
        </Fragment>
    );
}

export default MainHeaderComponent;
