import React, { Fragment, Suspense, useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { appInitAction } from './store/app/actions';
import { BrowserRouter } from 'react-router-dom';
import Routes from './Routes';
import MainHeader from './components/MainHeader/MainHeader';
import { tokenSelector } from './store/auth/selectors';

function App() {
    const isAuth = useSelector(tokenSelector) || false;
    const dispatch = useDispatch();
    const init = useCallback(() => dispatch(appInitAction()), []);
    useEffect(() => init(), []);

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ return ~~~ */

    return (
        <Fragment>
            <BrowserRouter>
                {isAuth && <MainHeader/>}
                <Suspense fallback={<div>Loading</div>}>
                    <Routes/>
                </Suspense>
            </BrowserRouter>
        </Fragment>
    );
}

export default App;
