import React, { Fragment } from 'react';
import { Button, TextField } from '@material-ui/core';
import DialogAlert from '../../../../components/DialogAlert/DialogAlert';
import useLang from '../../../../lang/useLang';

function AddDeviceComponent(props) {
    const lang = useLang('devices');
    return (
        <Fragment>
            <DialogAlert
                isOpen={props.isOpen}
                title={lang('addDevice')}
                labelAgree={lang('button.add')}
                labelCancel={lang('button.cancel')}
                onAgree={props.onAgree}
                onCancel={props.onCancel}
                disabledAgree={props.value.length === 0}
                children={<TextField
                    label={lang('deviceName')}
                    value={props.value}
                    onChange={props.onChange}
                />}
            />
            <Button
                variant='outlined'
                onClick={props.onShowDialog}
                children={lang('addDevice')}
            />
        </Fragment>
    );
}

export default AddDeviceComponent;
