import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useLang from '../../lang/useLang';
import DevicesComponent from './DevicesComponent';
import { devicesSelector } from '../../store/device/selectors';
import { deviceAddAction, deviceRequestListAction } from '../../store/device/actions';

function Devices() {
    const lang = useLang('devices');
    const rows = useSelector(devicesSelector);
    const dispatch = useDispatch();
    const init = useCallback(() => dispatch(deviceRequestListAction()), []);
    useEffect(() => init(), []);

    const columns = [
        { field: 'status', headerName: ' ', width: 64 },
        { field: 'name', headerName: lang('deviceName'), flex: 1 },
        { field: 'pin', headerName: lang('pin') },
        { field: 'enabled', headerName: lang('status') },
    ];

    const handleAddDevice = useCallback(name => {
        dispatch(deviceAddAction(name))
    }, []);

    const [stateSwitch, setStateSwitch] = useState(false);
    const handleSwitch = e => {
        setStateSwitch(e.target.checked);
    }

    return (
        <DevicesComponent
            rows={rows || []}
            columns={columns}
            onAddDevice={handleAddDevice}
            showPin={stateSwitch}
            onSwitch={handleSwitch}
        />
    );
}

export default Devices;
