import React from 'react';
import { useStyles } from './MessengerStyles';
import MessengerMessage from './MessengerMessage';

function MessengerComponents(props) {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            {props.messages.map(content => (
                <MessengerMessage
                    key={content.id}
                    message={content}
                    onClose={props.onCloseMessage}
                />
            ))}
        </div>
    );
}

export default MessengerComponents;