import React, { Fragment } from 'react';
import { useSelector } from 'react-redux';
import { tokenSelector } from './store/auth/selectors';
import { Route, Switch } from 'react-router-dom';
import RefreshToken from './components/Auth/RefreshToken/RefreshToken';
import ProtectedRoute from './components/Auth/ProtectedRoute/ProtectedRoute';
import SignIn from './pages/SignIn/SignIn';
import Home from './pages/Home/Home';
import NoMatch from './pages/NoMatch/NoMatch';
import Devices from './pages/Devices/Devices';

export const ROUTE_HOME = '/';
export const ROUTE_DEVICES = '/devices';

export const pageList = [
    {
        key: 'home',
        path: ROUTE_HOME,
        exact: true,
        component: Home,
        icon: 'dashboard',
        label: 'title',
    },
    {
        key: 'devices',
        path: ROUTE_DEVICES,
        exact: false,
        component: Devices,
        icon: 'important_devices',
        label: 'title',
    },
];

function Routes() {
    const isAuth = useSelector(tokenSelector) || false;
    if (isAuth === false) {
        return (<SignIn/>);
    }

    return (
        <Fragment>
            <Switch>
                {pageList.map(item => {
                    const { isProtected, key, ...otherProps } = item;
                    return isProtected ? (
                        <ProtectedRoute condition={isAuth} key={key} {...otherProps}/>
                    ) : (
                        <Route key={key} {...otherProps}/>
                    );
                })}
                <Route path="*" component={NoMatch}/>
            </Switch>
            <RefreshToken/>
        </Fragment>
    );
}

export default Routes;
