import React from 'react';
import MessengerComponents from './MessengerComponents';

import { connect } from 'react-redux';
import { messageHideAction } from '../../store/app/actions';
import { messagesSelector } from '../../store/app/selectors';

function Messenger(props) {
    return (
        <MessengerComponents
            messages={props.messages}
            onCloseMessage={props.onCloseMessage}
        />
    )
}

const mapStateToProps = store => ({
    messages: messagesSelector(store),
});

const mapDispatchToProps = dispatch => ({
    onCloseMessage: payload => dispatch(messageHideAction(payload)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Messenger);