import { all, call, put, select, takeLatest } from 'redux-saga/effects';
import { loaderToggleAction, messageShowAction } from '../app/actions';
import { DEVICE_ADD, DEVICE_REQUEST_LIST, deviceSetListAction } from './actions';
import { devicesSelector } from './selectors';
import { addDeviceRequest, getDeviceListRequest } from '../../requests/device';
import { getLang } from '../../lang/useLang';

const lang = getLang('devices');

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Workers ~~~ */

function* workerDeviceInit() {
    yield put(loaderToggleAction(true));
    try {
        const { devices } = yield call(getDeviceListRequest);
        yield put(deviceSetListAction(devices));
    } catch (error) {
        console.error('workerDeviceInit', error);
        yield put(messageShowAction({
            severity: 'error',
            message: getLang('common')('message.unknown'),
            lifeTime: 3000
        }));
    }
    yield put(loaderToggleAction(false));
}

function* workerDeviceAdd({ payload }) {
    yield put(loaderToggleAction(true));
    try {
        const { deviceCreate } = yield call(addDeviceRequest, { name: payload });
        const devices = yield select(devicesSelector);
        yield put(deviceSetListAction([...devices, deviceCreate]));
        yield put(messageShowAction({
            severity: 'success',
            message: lang('message.success')
        }));
    } catch (error) {
        console.error('workerDeviceAdd', error);
        yield put(messageShowAction({
            severity: 'error',
            message: getLang('common')('message.unknown'),
            lifeTime: 3000
        }));
    }
    yield put(loaderToggleAction(false));
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Watchers ~~~ */

export function* watchDeviceInit() {
    yield takeLatest(DEVICE_REQUEST_LIST, workerDeviceInit);
}

export function* watchDeviceAdd() {
    yield takeLatest(DEVICE_ADD, workerDeviceAdd);
}

export default function* () {
    yield all([
        watchDeviceInit(),
        watchDeviceAdd(),
    ])
}
