import React from 'react';
import useStyles from './DialogAlertStyles';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core';
import useLang from '../../lang/useLang';

function DialogAlertComponent(props) {
    const lang = useLang('common');
    const classes = useStyles();
    return (
        <Dialog
            open={props.open}
            onClose={props.onClose}
        >
            <DialogTitle
                children={props.title}
            />
            <DialogContent
                className={classes.content}
                children={props.content}
            />
            <DialogActions>
                {!props.hideAgree && (<Button
                    color='primary'
                    onClick={props.onAgree}
                    children={props.labelAgree || lang('yes')}
                    disabled={props.disabledAgree}
                />)}
                {!props.hideCancel && (<Button
                    color='default'
                    onClick={props.onClose}
                    children={props.labelCancel || lang('no')}
                />)}
            </DialogActions>
        </Dialog>
    );
}

export default DialogAlertComponent;
