import React from 'react';
import PropTypes from 'prop-types'
import DialogAlertComponent from './DialogAlertComponent';

function DialogAlert(props) {

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ handleAgree ~~~ */

    const handleAgree = () => {
        props.onAgree();
        handleClose();
    };

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ handleClose ~~~ */

    const handleClose = () => {
        props.onCancel();
    };

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ return ~~~ */

    return (
        <DialogAlertComponent
            open={props.isOpen}
            title={props.title}
            content={props.children}
            labelAgree={props.labelAgree}
            labelCancel={props.labelCancel}
            hideAgree={props.hideAgree}
            hideCancel={props.hideCancel}
            onAgree={handleAgree}
            onClose={handleClose}
            disabledAgree={props.disabledAgree}
        />
    );
}

export default DialogAlert;

DialogAlert.propTypes = {
    title: PropTypes.string,
    children: PropTypes.node.isRequired,
    isOpen: PropTypes.bool,
    labelAgree: PropTypes.string,
    labelCancel: PropTypes.string,
    onAgree: PropTypes.func,
    onCancel: PropTypes.func,
    disabledAgree: PropTypes.bool,
}

DialogAlert.defaultProps = {
    isOpen: false,
    onAgree: () => null,
    onCancel: () => null,
    disabledAgree: false,
}
