import { client } from './client';
import { gql } from 'graphql-request';

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ getDeviceListRequest ~~~ */

export function getDeviceListRequest() {
    const query = gql`query {
        devices {
            id
            name
            pin
            enabled
        }
    }`;
    return client().request(query)
        .then(data => data)
        .catch(error => console.error('getDeviceListRequest', error));
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ addDeviceRequest ~~~ */

export function addDeviceRequest({ name }) {
    const query = gql`mutation (
        $name: String!,
    ){
        deviceCreate(
            name: $name
        ){
            id
            name
            pin
            enabled
        }
    }`;
    return client().request(query, { name })
        .then(data => data)
        .catch(error => console.error('addDeviceRequest', error));
}
