export const APP_INIT = 'APP_INIT';
export const LOADER_TOGGLE = 'LOADER_TOGGLE';

export const MESSAGE_SHOW = 'MESSAGE_SHOW';
export const MESSAGE_HIDE = 'MESSAGE_HIDE';
export const MESSAGE_SET_LIST = 'MESSAGE_SET_LIST';

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Actions  ~~~ */

export const appInitAction = () => ({
    type: APP_INIT,
});

export const loaderToggleAction = payload => ({
    type: LOADER_TOGGLE,
    payload: payload,
});

export const messageShowAction = ({ severity = 'info', title = null, message = null, lifeTime = 1500 }) => ({
    type: MESSAGE_SHOW,
    payload: ({ severity, title, message, lifeTime }),
});

export const messageHideAction = ({ id }) => ({
    type: MESSAGE_HIDE,
    payload: id,
});

export const messageSetListAction = payload => ({
    type: MESSAGE_SET_LIST,
    payload: payload,
});