/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Actions ~~~ */

export const DEVICE_REQUEST_LIST = 'DEVICE_REQUEST_LIST';
export const DEVICE_SET_LIST = 'DEVICE_SET_LIST';
export const DEVICE_ADD = 'DEVICE_ADD';

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ ActionsCreator ~~~ */

export const deviceRequestListAction = () => ({
    type: DEVICE_REQUEST_LIST,
});

export const deviceSetListAction = payload => ({
    type: DEVICE_SET_LIST,
    payload: payload,
});

export const deviceAddAction = payload => ({
    type: DEVICE_ADD,
    payload: payload,
});
