import React, { useEffect } from 'react';
import useLang from '../../lang/useLang';
import { useStyles } from './MessengerStyles';
import { Alert, AlertTitle } from '@material-ui/lab';

function MessengerMessage({ message, onClose }) {
    const lang = useLang('messenger');
    const classes = useStyles();

    useEffect(() => {
        const timeout = setTimeout(() => {
            onClose(message);
            clearTimeout(timeout);
        }, message.lifeTime);
    }, [message, onClose]);

    /*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ getTitle ~~~*/

    const getTitle = () => {
        if (message.title) {
            return message.title;
        }
        switch (message.severity) {
            case 'success':
                return lang('success');
            case 'error':
                return lang('error');
            case 'warning':
                return lang('warning');
            default:
                return lang('info');
        }
    }
    /*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ return ~~~*/

    if (!message) return null;
    return (
        <Alert
            className={classes.message}
            severity={message.severity}
            onClose={() => onClose(message)}
        >
            <AlertTitle children={getTitle()}/>
            {message.message}
        </Alert>
    );
}

export default MessengerMessage;