import * as ru from './ru';

export function getLang(locate) {
    const lang = ru[locate] || {};
    return key => {
        const explode = key.split('.', 2);
        if (explode.length === 1) return lang[key] || key;
        try {
            return lang[explode[0]][explode[1]];
        } catch (e) {
            return key;
        }
    };
}

function useLang(locate) {
    return getLang(locate);
}

export default useLang;
