import { all, put, select, takeEvery, takeLatest } from 'redux-saga/effects';
import { APP_INIT, MESSAGE_HIDE, MESSAGE_SHOW, messageSetListAction, messageShowAction } from './actions';
import { messagesSelector } from './selectors';
import { getLang } from '../../lang/useLang';

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Workers ~~~ */

let messageId = 0;

function* workerAppInit() {
    const lang = getLang('common');
    try {

    } catch (error) {
        console.error('workerAppInit', error);
        yield put(messageShowAction({
            severity: 'error',
            message: lang('message.unknown'),
            lifeTime: 3000
        }));
    }
}

function* workerPutMessage({ payload }) {
    const messages = yield select(messagesSelector);
    yield put(messageSetListAction([...messages, { id: messageId++, ...payload }]));
}

function* workerRemoveMessage({ payload }) {
    const messages = yield select(messagesSelector);
    const newMessages = messages.filter(message => message.id !== payload);
    yield put(messageSetListAction(newMessages));
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Watchers ~~~ */

export function* watchAppInit() {
    yield takeLatest(APP_INIT, workerAppInit);
}

export function* watchPutMessage() {
    yield takeEvery(MESSAGE_SHOW, workerPutMessage);
}

export function* watchRemoveMessage() {
    yield takeEvery(MESSAGE_HIDE, workerRemoveMessage);
}

export default function* () {
    yield all([
        watchAppInit(),
        watchPutMessage(),
        watchRemoveMessage(),
    ])
}
