import React, { useCallback, useState } from 'react';
import PropTypes from 'prop-types'
import { useDispatch, useSelector } from 'react-redux';
import LoginFormComponent from './LoginFormComponent';
import { authSignIn } from '../../../store/auth/actions';
import { isLoadingSelector } from '../../../store/app/selectors';

function LoginForm(props) {
    const isLoading = useSelector(isLoadingSelector);
    const dispatch = useDispatch();
    const authLoginRequest = useCallback(payload => dispatch(authSignIn(payload)), []);

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ fields ~~~ */

    const [fields, setFields] = useState({ email: '', password: '', remember: false });
    const changeField = ({ target }) => {
        const { name, value, checked } = target;
        if (name === 'remember') {
            setFields(prevState => ({ ...prevState, [name]: checked }));
            return;
        }
        setFields(prevState => ({ ...prevState, [name]: value }));
    }

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ handleDisableSubmit ~~~ */

    const handleDisableSubmit = () => {
        const { email, password } = fields;
        return (email === '' || password === '');
    }

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ formAction ~~~ */

    const formAction = event => {
        event.preventDefault();
        authLoginRequest(fields);
    }

    /* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ return ~~~ */

    return (<LoginFormComponent
        header={props.header}
        isLoading={isLoading}
        disableSubmit={handleDisableSubmit()}
        fields={fields}
        changeField={changeField}
        onSubmit={formAction}
    />);
}

LoginForm.propTypes = {
    header: PropTypes.node
}

export default LoginForm;
